<template>
  <div class="container mx-auto p-4 max-w-md">
    <h1 class="text-2xl font-bold mb-4 text-center">管理者登入</h1>
    <form @submit.prevent="handleLogin" class="bg-white p-6 rounded-lg shadow-md">
      <div class="mb-4">
        <label for="username" class="block text-sm font-medium text-gray-700">使用者名稱</label>
        <input
          type="text"
          id="username"
          v-model="username"
          class="mt-1 block w-full p-2 border border-gray-300 rounded-md"
          required
        />
      </div>
      <div class="mb-4">
        <label for="password" class="block text-sm font-medium text-gray-700">密碼</label>
        <input
          type="password"
          id="password"
          v-model="password"
          class="mt-1 block w-full p-2 border border-gray-300 rounded-md"
          required
        />
      </div>
      <button
        type="submit"
        class="w-full bg-blue-600 text-white p-2 rounded-md hover:bg-blue-700"
      >
        登入
      </button>
      <p v-if="error" class="mt-4 text-red-600 text-center">{{ error }}</p>
    </form>
  </div>
</template>

<script>
import { useAuthStore } from '@/stores/auth'
import { useRouter } from 'vue-router'

export default {
  name: 'LoginView',
  setup() {
    const authStore = useAuthStore()
    const router = useRouter()

    return {
      authStore,
      router
    }
  },
  data() {
    return {
      username: '',
      password: '',
      error: ''
    }
  },
  methods: {
    handleLogin() {
      const success = this.authStore.login(this.username, this.password)
      if (success) {
        this.router.push('/')
      } else {
        this.error = '使用者名稱或密碼錯誤'
      }
    }
  }
}
</script>